
.closeActiveCase {
    display: none;
}
.caseActive_container {
    position: fixed;
    max-width: 1030px;
    max-height: 600px;
    min-height: 250px;
    background-color: #2B2A61;
    padding: 10px 10px 40px;
    border-radius: 20px;
    z-index: 20;
    box-shadow: 0 0 1000px 1000px #0009;
    transition: box-shadow .7s ease;
    top: 13%;
    left: calc(100% - 80%);
    right: calc(100% - 80%);
}
.caseActive_title {
    display: flex;
    justify-content: right;
    width: 100%;
}
.caseActive_buttonClose {
    background-color: transparent;
    border: none;
    &:hover span {
        text-shadow: 0 0px 20px #63B9F7;
    }
    & span {
        color: #fff;
        max-height: 40px;
        font-size: 30px;
    }
}
.caseActive_content {
    padding: 10px 40px 40px;
}
.caseActive_imageAndTitle {
    display: flex;
    gap: 50px;
}
.caseActive_imageAndTitle_image img {
    max-width: 345px;
    border-radius: 15px;
    pointer-events: none;
}
.caseActive_imageAndTitle_title {
    display: flex;
    flex-direction: column;
}
.caseActive_imageAndTitle_title_item {
    display: flex;
    gap: 50px;
    align-items: center;
}
.caseActive_imageAndTitle_title_item:nth-child(1) {
    margin-bottom: 5px;
}
.caseActive_imageAndTitle_title_item_main {
    max-width: 50px;
    color: #fff3;
    font-size: 24px;
    font-weight: 500;
}
.caseActive_imageAndTitle_title_item_name {
    color: #fff;
    font-size: 16px;
    font-weight: 400;
}
.caseActive_imageAndTitle_title_item_stacks {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
}
.caseActive_imageAndTitle_title_item_stack {
    background-color: rgb(99, 185, 247, .2);
    border-radius: 10px;
    padding: 2px 10px;
}
.caseActive_imageAndTitle_title_item_stack span {
    color: #fff;
    font-size: 12px;
    font-weight: 100;
}
.caseActive_imageAndTitle_title_button {
    width: 100%;
    display: flex;
    margin-top: 25px;
}
.caseActive_imageAndTitle_title_button_link {
    border: 1px solid #fff5;
    max-width: 250px;
    position: relative;
    color: #fff;
    text-decoration: none;
    font-size: 14px;
    font-weight: 100;
    padding: 10px 40px;
    border-radius: 50px;
    overflow: hidden;
    transition: all .5s ease;
    &:hover {
        padding: 10px 60px 10px 40px;
    }
    &:hover img {
        transform: translateX(0);
    }
}

.caseActive_imageAndTitle_title_button_link img {
    position: absolute;
    right: 30px;
    max-width: 25px;
    opacity: .5;
    transform: translateX(100px);
    transition: transform .5s ease;
    pointer-events: none;
}

.caseActive_abouts {
    margin: 20px 0 0 20px;
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.caseActive_about {
    color: #fff;
    font-size: 14px;
    font-weight: 200;
    position: relative;
}
.caseActive_about::before {
    content: '';
    position: absolute;
    left: -22px; top: 4px;
    width: 12px;
    height: 12px;
    background-color: #63B9F7;
    border-radius: 50%;
}