.codeScreen_container {
    margin: 70px 60px;
    position: relative;
}
.codeScreen_image {
    width: 100%;
    border-radius: 20px;
    position: relative;
    pointer-events: none;
    max-height: 200px;
}
.codeScreen_container::before {
    content: '';
    background-color: #2B2A61;
    width: 100%;
    height: 100%;
    position: absolute;
    bottom: -10px; right: -15px;
    border-radius: 20px;
}
.codeScreen_container.light::before {
    background-color: rgba(43, 42, 97, .7);
}

@media ( max-width: 900px ) {
    .codeScreen_container {
        margin: 70px 20px;
    }
    .codeScreen_image {
        border-radius: 15px;
    }
    .codeScreen_container::before {
        bottom: -5px; right: -10px;
        border-radius: 15px;
    }
}
@media ( max-width: 450px ) {
    .codeScreen_image {
        border-radius: 10px;
    }
    .codeScreen_container::before {
        bottom: -2px; right: -7px;
        border-radius: 10px;
    }
}