.contact_container {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 140px 0 40px;
}
.contact_content_Form {
    display: flex;
    width: 100%;
    gap: 150px;
    max-width: 100%;
}
.contact_content {
    width: 420px;
    margin-left: 170px;
}
.contact_content_p1,
.contact_content_p2,
.contact_form_title,
.form_title,
.form_item_title1,
.form_item_title2,
.form_item_title3,
.form_item_title4 {
    color: #4C4B80;
    font-size: 16px;
    font-weight: 400;
}
.contact_content_p1.light,
.contact_content_p2.light,
.contact_form_title.light {
    color: rgb(76, 75, 128, .5);
}
.contact_form_title {
    margin-bottom: 35px;
}
form {
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.form_item {
    display: flex;
    margin-left: 20px;
}
.form_item_title1,
.form_item_title2,
.form_item_title3,
.form_item_title4 {
    width: 100px;
}
.marksOne1,
.marksOne2,
.marksTwo1,
.marksTwo2,
.marksThree1,
.marksThree2,
.marksFour1,
.marksFour2 {
    color: #4C4B80;
    font-size: 16px;
    margin-right: 5px;
}

.form_item_input1 input,
.form_item_input2 input,
.form_item_input3 input,
.form_item_input4 input {
    background-color: transparent;
    border: none;
    width: 170px;
    caret-color: #63B9F7;
    color: #63B9F7;
    font-size: 16px;
    font-weight: 500;
    margin-right: 5px;
}
.form_item_input1 input:focus, 
.form_item_input2 input:focus, 
.form_item_input3 input:focus, 
.form_item_input4 input:focus {
    outline: none;
}
.form_item_input1 input:focus::placeholder, 
.form_item_input2 input:focus::placeholder, 
.form_item_input3 input:focus::placeholder, 
.form_item_input4 input:focus::placeholder {
    opacity: 0;
    transition: opacity .3s linear;
}

.form_item_input1 input::placeholder,
.form_item_input2 input::placeholder,
.form_item_input3 input::placeholder,
.form_item_input4 input::placeholder {
    background: linear-gradient(45deg, #B931F9 0%, #63B9F7 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 16px;
    font-weight: 600;
}
.colorForm {
    color: #63B9F7;
}

.contact_content_info {
    margin: 60px;
}
.contact_content_info_p {
    color: #4C4B80;
}
.form_button_btn {
    border: none;
    background-color: transparent;
    font-size: 16px;
    color: #fff;
    font-family: "Poppins", sans-serif;
    margin-top: 35px;
}
.form_button_btn-bracket {
    color:#B931F9;
}
.form_button_btn-form {
    color: #63B9F7;
}
.contact_form {
    margin-right: 60px;
}
.contact_social {
    width: 100%;
    display: flex;
    justify-content: center;
}
.contact_social_content {
    display: flex;
    gap: 20px;
    position: relative;
}

@media ( max-width: 1050px ) {
    .contact_content_Form {
        gap: 100px;
    }
}
@media ( max-width: 950px ) {
    .contact_content_Form {
        gap: 50px;
    }
}
@media ( max-width: 850px ) {
    .contact_content_Form {
        gap: 50px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .contact_content {
        width: 100%;
        margin-left: 0;
        padding: 0 40px;
    }
    .contact_form {
        margin-right: 0;
        margin: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .contact_form_title {
        margin-bottom: 30px;
        width: 100%;
        max-width: 400px;
        margin: 0 80px;
    }
    .contact_form_form {
        max-width: 400px;
    }
    .contact_content_info {
        margin: 60px 40px;
    }
}
@media ( max-width: 600px ) {
    .contact_social_content {
        /* width: 100%; */
        margin: 0 40px;
        gap: 20px;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
    }
}
@media ( max-width: 490px ) {
    .contact_content {
        width: 100%;
        margin-left: 0;
        padding: 0 20px;
    }
    .contact_form_form {
        padding: 0 20px;
        max-width: 100%;
    }
    .contact_form_title {
        max-width: 100%;
        padding: 0 40px 20px;
    }
    .contact_content_info {
        margin: 40px 20px;
    }
}

@media ( max-width: 300px ) {
    .contact_social_content {
        margin: 0 20px;
    }
}