.reviewCard_container {
    background-color: #2B2A61;
    border-radius: 50px;
    position: relative;
    width: 87%;
    min-height: 160px;
    padding: 30px 40px 30px 55px;
    margin: 0 40px;
}
.reviewCard_image {
    position: absolute;
    top: 25%;
    left: -35px;
    width: 75px;
    height: 75px;
    mask: url('../../../images/maskUsers.png');
    mask-size: 75px;
}
.reviewCard_text {
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.reviewCard_name {
    font-size: 16px;
    font-weight: 600;
    color: #fff;
}
.reviewCard_message {
    font-size: 14px;
    font-weight: 300;
    color: #fff9;
}

@media ( max-width: 1340px ) {
    .reviewCard_container {
        max-height: 170px;
    }
}
@media ( max-width: 980px ) {
    .reviewCard_container {
        padding: 30px 20px 30px 55px;
    }
    .reviewCard_image {
        top: 30%;
        left: -27px;
        width: 65px;
        height: 65px;
        mask-size: 65px;
    }
}
@media ( max-width: 650px ) {
    .reviewCard_container {
        width: 85%;
        min-height: 190px;
        padding: 30px 20px 30px 45px;
    }
}
@media ( max-width: 520px ) {
    .reviewCard_container {
        width: 90%;
        min-height: 150px;
        max-height: 300px;
    }
    .reviewCard_image {
        top: 28%;
        left: -25px;
        width: 60px;
        height: 60px;
        mask-size: 60px;
    }
}
@media ( max-width: 375px ) {
    .reviewCard_container {
        width: 83%;
    }
    .reviewCard_image {
        top: 35%;
        left: -20px;
        width: 50px;
        height: 50px;
        mask-size: 50px;
    }
}